*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-repeat: none;
}
:root{
    width: 100vw;
    height: 100vh;
}
.main-div{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #42d48fe8;
}
.center-div{
    box-shadow: 11px 13px 19px 4px rgb(0 0 0 / 32%);
    background: white;
    border-radius: 10px;
    width: 27vw;
    height: 70vh;
}
h1{
    text-align: center;
    text-shadow: 16px 0px 5px rgb(0 0 0 / 20%);
    font-size: 50px;
    font-family: sans-serif;
    letter-spacing: 4px;
}
input{
    border: none;
    outline: none;
    border-bottom: 3px solid #ed43c7;
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    /* margin: 0 41px; */
    margin-left: 65px;
}
li{
    list-style: none;
    margin-left: 43px;
    margin-top: 5px;
    font-size: 25px;
    font-style: normal;
    font-family: sans-serif;
    font-weight: 500;
    color:rgb(220, 106, 125);
}
#icon{
    margin-right:10px;
    color: white;
    background-color:rgb(176, 23, 176) ;
    border-radius: 13px;
}
.c1:hover{
    background-color: rgb(217, 96, 116);
    color: white;
}
#icon:hover{
    color:black;
    background-color: white;
}
@media only screen and (max-width:500px){
    .center-div{
        width: 85vw;
    }
}